  font-weight: bold;
  box-sizing: border-box;
}

.onoffswitch-inner:before {
  content: 'ON';
  padding-left: 7px;
  background-color: #36a5ca;
  color: #ffffff;
}

.onoffswitch-inner:after {
  content: 'OFF';
  padding-right: 7px;
  background-color: #ffffff;
  color: #919191;
  text-align: right;
}

.onoffswitch-switch {
  display: block;
  width: 18px;
  margin: 0;
  background: #ffffff;
  border: 2px solid #36a5ca;
  border-radius: 3px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 36px;
  transition: all 0.3s ease-in 0s;
}

.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
  margin-left: 0;
}

