.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
}

.slider::before {
  position: absolute;
  content: '';
  height: 13px;
  width: 13px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider {
  /* background-color: #0000ff; */
  background-color: #6178a1;
  width: 42px;
  min-width: inherit;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider::before {
  transform: translateX(19px);
}

/* Rounded sliders */

.slider.round {
  border-radius: 34px;
  width: 42px;
  min-width: inherit;
}

.slider.round::before {
  border-radius: 50%;
}

.switch {
  position: relative;
