/*Radio*/

input.radio {
  display: none;
}

input[type='radio'] + label.radio {
  cursor: pointer;
  position: relative;
  padding-left: 24px;
  margin: 0px;
  clear: both;
  display: inline-block;
  line-height: 18px;
  text-align: left;
}

input[type='radio'] + label.radio:before {
  content: '';
  width: 18px;
  height: 18px;
  position: absolute;
  top: 0px;
  left: 0px;
  font-family: FontAwesome;
  text-align: center;
  color: #fff;
  font-size: 0px;
  border-radius: 100px;
  border: 2px solid #8a8f95;
  line-height: 15px;
}

input[type='radio']:checked + label.radio:before {
  content: '\f00c';
  border: 2px solid #60c138;
  font-size: 11px;
  color: #fff;
  background: #60c138;
}

input[type='radio']:checked + label.radio {
  color: #60c138;
}

.radio_group input[type='radio'] + label.radio,
.checkbox_group input[type='checkbox'] + label.checkbox {
  margin: 5px 15px 5px 0px;
  float: left;
  clear: none;
}

.radio_group span.label,
.checkbox_group span.label,
.checkbox_group label.label,
.radio_group label.label,
.form-group label.label {
  color: #333;
  display: inline-block;
  padding: 0px;
  font-size: 14px;
  font-weight: normal;
  line-height: normal;
  color: #333;
  text-align: left;
  white-space: normal;
  vertical-align: baseline;
  border-radius: 0px;
  margin-right: 10px;
}

.checkbox_group label.label,
