  margin-right: 8px;
  position: relative;
  transition: all 0.3s;
  color: #626262;
  cursor: pointer;
  line-height: 1.5;
}

.rc-rate-star-first {
  position: absolute;
  left: 0;
  top: 0;
  width: 50%;
  height: 100%;
  overflow: hidden;
  opacity: 0;
}

.rc-rate-star-first,
.rc-rate-star-second {
  transition: all 0.3s;
}

.rc-rate-star-half .rc-rate-star-first,
.rc-rate-star-full .rc-rate-star-second {
  color: #0000ff;
}
.nav-tabs.nav-justified > li > a {
  border: none !important;
  margin-right: 2px;
}

input[type='file']::file-selector-button {
  border: 2px solid #4b4bff;
  padding: 0.3em 0.5em;
  color: white;
  border-radius: 0.2em;
  background-color: #4b4bff;
}
.vehicle-photo-preview {
  width: 320px !important;
  height: 180px !important;
  object-fit: cover !important;
  padding: 0.75rem;
}
